input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type="number"]{-moz-appearance:textfield}#dot{display:flex;justify-content:space-around;align-items:center}#dot .dot{margin-top:50px;height:500px;width:500px;background-color:#bbb;border-radius:50%;display:inline-block;font-size:70px;display:flex;justify-content:center;align-items:center}

#player{width:350px;height:50px;position:relative;margin-right:0;top:80px;margin-left:20px}#player i{font-size:18px;position:absolute;margin-top:-6px;color:#666}#player i.fa-volume-down{margin-left:-8px}#player i.fa-volume-up{margin-right:-8px;right:0}#volume{position:absolute;left:24px;margin:0 auto;height:5px;width:300px;background:#555;border-radius:15px}

#game{max-height:100vh}#game .wrap{margin:0 auto;margin-top:40px;width:560px}#game .square{float:left}#game .buttons{position:absolute;right:50px}#game button{margin:10px}

